<template>
  <v-container
    fluid
    grid-list-md>
    <v-form
      ref="form2"
      @submit.prevent="validateBeforeSubmit">
      <v-layout
        row
        wrap>
        <v-flex md12>
          <v-card class="elevation-0">
            <v-card-title>
              <h3 class="headline mb-0">Escolha a empresa a pagar / Loja</h3>
            </v-card-title>
            <v-card-text>
              <core-filtro-rede-empresa-loja
                v-model="value"
                :on-change-rede="onChangeRede"
                :on-change-empresa="onChangeEmpresa"
                :loja-selecionada="lojaSelecionada"
              />
            </v-card-text>
          </v-card>
        </v-flex>
        <v-flex md12>
          <v-layout class="justify-end">
            <v-btn
              :disabled="!value.lojaId"
              :loading="loadingBtn"
              type="submit"
              color="primary">Continuar</v-btn>
          </v-layout>
        </v-flex>
      </v-layout>
    </v-form>
  </v-container>
</template>

<script>
export default {
  props: {
    value: {
      type: Object,
      required: true
    },
    // eslint-disable-next-line vue/require-default-prop
    setData: {
      type: Function,
      requided: true
    },
    // eslint-disable-next-line vue/require-default-prop
    nextStep: {
      type: Function
    }
  },
  data () {
    return {
      loadingBtn: false
    }
  },
  watch: {
    value () {
      this.$emit('input', this.value)
    }
  },
  methods: {
    validateBeforeSubmit () {
      this.$validator.validate().then(result => {
        if (result) {
          this.nextStep()
        } else {
          this.$root.showAlerta(this.$vuetify.t('$vuetify.Erro.camposObrigatorios'))
        }
      })
    },
    onChangeRede (rede) {
      this.value.redeNome = rede.descricao
      this.clear()
    },
    onChangeEmpresa (empresa) {
      this.value.empresaNome = empresa.descricao
      this.clear()
    },
    lojaSelecionada (loja) {
      this.value.lojaNome = loja.descricao
      this.value.lojaId = loja.id
    },
    clear () {
      this.value.lojaId = null
    }
  }
}
</script>
